import React, { Fragment } from "react";
import dollarImg from "../../assets/dollar.png";
import "../ReviewTransactions/index.css";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBell,
  faBitcoinSign,
  faChevronLeft,
  faExchange,
  faPaperPlane,
  faQrcode,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";
import Select from "react-select";
import CoinItemChart from "../../components/CoinItemChart";
import TransactionItem from "../../components/TransactionItem";
import QRCode from "react-qr-code";
import Input from "../../shared/Input";
import Button from "../../shared/Button";
import USDTImg from "../../assets/usdt.png";
import BTCImg from "../../assets/bitcoin.png";
import ethereumImg from "../../assets/ethereum.png";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
const TransactionDetails = () => {
  const User = useSelector(({ User }) => User);
  const Eth = useSelector(({ Eth }) => Eth);
  const Btc = useSelector(({ Btc }) => Btc);
  const Usdt = useSelector(({ Usdt }) => Usdt);
  const { selectedCoin } = User;
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const coinMappings = {
    BTC: {
      circleColor: "yellow-circle",
      baseColor: "yellow",
      selectedReducer: Btc,
    },
    ETH: {
      circleColor: "blue-circle",
      baseColor: "blue",
      selectedReducer: Eth,
    },
    USDT: {
      circleColor: "green-circle",
      baseColor: "green",
      selectedReducer: Usdt,
    },
  };

  const selectedCoinData = coinMappings[selectedCoin];

  const { circleColor, baseColor, selectedReducer } = selectedCoinData;

  const { selectedTransaction } = selectedReducer;

  const { type, receiver, amount, fee, date } = selectedTransaction;
  const options = {
    hour: "2-digit",
    minute: "2-digit",
    month: "short",
    day: "numeric",
    year: "numeric",
    hour12: true,
  };
  const formattedDate = new Date(date).toLocaleString("en-US", options);
  const feeCoin = selectedCoin === "USDT" ? "ETH" : selectedCoin;
  const totalAmount =
    selectedCoin === "USDT" ? amount : parseFloat(fee) + parseFloat(amount);
  const image =
    selectedCoin === "BTC"
      ? BTCImg
      : selectedCoin === "ETH"
      ? ethereumImg
      : USDTImg;
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div
          className={`wallet-header-container ${baseColor} review-transaction--header`}
        >
          <div className={`wallet-header-circle ${circleColor}`} />
          <div className="wallet-header-content">
            <div className="review-transaction-row">
              <div className="review-transaction-text">Review Transaction</div>
              <div
                className="review-transaction-icon-container"
                onClick={() => navigate("/coinwallet")}
              >
                <FontAwesomeIcon icon={faTimes} />
              </div>
            </div>
            <div className="review-transaction-text">
              <img src={image} className="review-transaction-image" />
              <div className="review-transaction-value">
                {amount} <span>{selectedCoin}</span>
              </div>
            </div>
          </div>
        </div>
        <div className="review-transaction-body-container">
          <div className="review-transaction-status-row">
            <div className="review-transaction-status-text">Status:</div>
            <div className="review-transaction-status-value">Complete</div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">Type</div>
            <div className="reveiw-transcation-item-value">{type}</div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">Date</div>
            <div className="reveiw-transcation-item-value">{formattedDate}</div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">Amount</div>
            <div className="reveiw-transcation-item-value">
              {amount} {selectedCoin}
            </div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">
              Transaction Fee:
            </div>
            <div className="reveiw-transcation-item-value">
              {fee} {feeCoin}
            </div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">Total Amount:</div>
            <div className="reveiw-transcation-item-value">
              {totalAmount} {selectedCoin}
            </div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">From:</div>
            <div className="reveiw-transcation-item-value">
              {selectedCoin} Wallet
            </div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">To:</div>
            <div className="reveiw-transcation-item-value">{receiver}</div>
          </div>
        </div>
        <div className="review-btn-container">
          <Button text={"View on Blockchain"} className={`${baseColor}-alt`} />
        </div>
      </div>
    </Fragment>
  );
};

export default TransactionDetails;
