import React from "react";
import "../CreatePin/index.css";
import FourDigits from "../../shared/FourDigits";
import Button from "../../shared/Button";
import withTabBar from "../../HOC/TabBar";
import { useDispatch, useSelector } from "react-redux";
import { setUser, updatePin } from "../../redux/actions/userAction";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";

const UpdatePin = () => {
  const User = useSelector(({ User }) => User);
  const { updateCurrentPin, updateNewPin, updateConfirmNewPin } = User;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const setCurrentPin = (digits) => {
    dispatch(
      setUser({
        name: "updateCurrentPin",
        value: digits,
      })
    );
  };
  const setUpdateNewPin = (digits) => {
    dispatch(
      setUser({
        name: "updateNewPin",
        value: digits,
      })
    );
  };
  const setUpdateConfirmNewPin = (digits) => {
    dispatch(
      setUser({
        name: "updateConfirmNewPin",
        value: digits,
      })
    );
  };
  const onClick = () => {
    const currentPin = updateCurrentPin.join("");
    const newPin = updateNewPin.join("");
    const confirmNewPin = updateConfirmNewPin.join("");
    if (newPin !== confirmNewPin) {
      Swal.fire({
        icon: "error",
        title: "Recheck Pins",
        text: "New Pin and Confirm New Pin are not same",
      });
      return;
    }
    dispatch(
      updatePin({
        data: {
          currentPin: currentPin,
          newPin: newPin,
        },
        navigate,
      })
    );
  };
  return (
    <div className="pin-container">
      <div className="transparent-circle" />
      <div className="pin-content">
        <div className="pin-header">Update Pin</div>
        <div className="pin-desc">Provide the current and new 4 digit pin.</div>
        <div className="pin-digits-container">
          <div className="pin-digits-row">
            <div className="pin-text">Enter Current 4 Digit Pin</div>
            <div className="pin-inputs-container">
              <FourDigits digits={updateCurrentPin} setDigits={setCurrentPin} />
            </div>
          </div>
          <div className="pin-digits-row">
            <div className="pin-text">Enter New 4 Digit Pin</div>
            <div className="pin-inputs-container">
              <FourDigits digits={updateNewPin} setDigits={setUpdateNewPin} />
            </div>
          </div>
          <div className="pin-digits-row">
            <div className="pin-text">Confirm New 4 Digit Pin</div>
            <div className="pin-inputs-container">
              <FourDigits
                digits={updateConfirmNewPin}
                setDigits={setUpdateConfirmNewPin}
              />
            </div>
          </div>
        </div>
        <div className="pin-btn-container">
          <Button text={"Update Pin"} onClick={onClick} />
        </div>
      </div>
    </div>
  );
};

export default withTabBar(UpdatePin);
