import React, { Fragment, useEffect } from "react";
import dollarImg from "../../assets/dollar.png";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBell,
  faBitcoinSign,
  faChevronLeft,
  faExchange,
  faPaperPlane,
  faQrcode,
  faTimes,
} from "@fortawesome/free-solid-svg-icons";
import Select from "react-select";
import CoinItemChart from "../../components/CoinItemChart";
import TransactionItem from "../../components/TransactionItem";
import QRCode from "react-qr-code";
import Input from "../../shared/Input";
import Button from "../../shared/Button";
import USDTImg from "../../assets/usdt.png";
import BTCImg from "../../assets/bitcoin.png";
import ethereumImg from "../../assets/ethereum.png";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { estimateUSDTGas } from "../../redux/actions/usdtAction";
import { estimateETHGas } from "../../redux/actions/ethAction";
import { estimateBTCGas } from "../../redux/actions/btcAction";
import { setUser } from "../../redux/actions/userAction";
const ReviewTransactions = () => {
  const User = useSelector(({ User }) => User);
  const Eth = useSelector(({ Eth }) => Eth);
  const Btc = useSelector(({ Btc }) => Btc);
  const Usdt = useSelector(({ Usdt }) => Usdt);
  const { selectedCoin } = User;
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const coinMappings = {
    BTC: {
      coinName: "Bitcoin",
      circleColor: "yellow-circle",
      baseColor: "yellow",
      selectedReducer: Btc,
    },
    ETH: {
      coinName: "Ethereum",
      circleColor: "blue-circle",
      baseColor: "blue",
      selectedReducer: Eth,
    },
    USDT: {
      coinName: "Tether",
      circleColor: "green-circle",
      baseColor: "green",
      selectedReducer: Usdt,
    },
  };

  const selectedCoinData = coinMappings[selectedCoin];

  const { selectedReducer, circleColor, baseColor } = selectedCoinData;

  const { sendAmount, recipientAddress, gasPrice } = selectedReducer;

  useEffect(() => {
    if (selectedCoin === "BTC") {
      dispatch(
        estimateBTCGas({
          data: {
            amount: sendAmount,
            recipientAddress,
          },
        })
      );
    } else if (selectedCoin === "ETH") {
      dispatch(
        estimateETHGas({
          data: {
            amount: sendAmount,
            recipientAddress,
          },
        })
      );
    } else if (selectedCoin === "USDT") {
      dispatch(
        estimateUSDTGas({
          data: {
            amount: sendAmount,
            recipientAddress,
          },
        })
      );
    }
  }, []);

  const onContinue = () => {
    navigate("/confirmPin");
  };

  const transactionFeeCoin = selectedCoin === "USDT" ? "ETH" : selectedCoin;
  const totalAmount =
    selectedCoin === "USDT"
      ? sendAmount
      : (parseFloat(gasPrice) + parseFloat(sendAmount)).toFixed(7);
  const image =
    selectedCoin === "BTC"
      ? BTCImg
      : selectedCoin === "ETH"
      ? ethereumImg
      : USDTImg;
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div
          className={`wallet-header-container ${baseColor} review-transaction--header`}
        >
          <div className={`wallet-header-circle ${circleColor}`} />
          <div className="wallet-header-content">
            <div className="review-transaction-row">
              <div className="review-transaction-text">Review Transaction</div>
              <div
                className="review-transaction-icon-container"
                onClick={() => navigate("/sendCoin")}
              >
                <FontAwesomeIcon icon={faTimes} />
              </div>
            </div>
            <div className="review-transaction-text">
              <img src={image} className="review-transaction-image" />
              <div className="review-transaction-value">
                {sendAmount} <span>{selectedCoin}</span>
              </div>
            </div>
          </div>
        </div>
        <div className="review-transaction-body-container">
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">Date</div>
            <div className="reveiw-transcation-item-value">
              11:18 PM on Sep 20, 2020
            </div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">Amount</div>
            <div className="reveiw-transcation-item-value">
              {sendAmount} {transactionFeeCoin}
            </div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">
              Transaction Fee:
            </div>
            <div className="reveiw-transcation-item-value">
              {gasPrice} {transactionFeeCoin}
            </div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">Total Amount:</div>
            <div className="reveiw-transcation-item-value">
              {totalAmount} {selectedCoin}
            </div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">From:</div>
            <div className="reveiw-transcation-item-value">
              {selectedCoin} Wallet
            </div>
          </div>
          <div className="review-transaction-item">
            <div className="review-transaction-item-header">To:</div>
            <div className="reveiw-transcation-item-value">
              {recipientAddress}
            </div>
          </div>
        </div>
        <div className="review-btn-container">
          <Button
            text={"Continue"}
            className={baseColor}
            onClick={onContinue}
          />
        </div>
      </div>
    </Fragment>
  );
};

export default ReviewTransactions;
