import React, { Fragment, useEffect } from "react";
import dollarImg from "../../assets/dollar.png";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBell } from "@fortawesome/free-solid-svg-icons";
import CoinItem from "../../components/CoinItem";
import withTabBar from "../../HOC/TabBar";
import { useDispatch, useSelector } from "react-redux";
import { getETHBalance, getETHPrices } from "../../redux/actions/ethAction";
import { getBTCBalance, getBTCPrices } from "../../redux/actions/btcAction";
import { getUSDTBalance, getUSDTPrices } from "../../redux/actions/usdtAction";

const Wallets = () => {
  const options = {
    scales: {
      x: {
        display: false, // Hide X axis labels
      },
      y: {
        display: false, // Hide Y axis labels
      },
    },
    plugins: {
      legend: {
        display: false, // Hide the legend
      },
    },
  };
  const dispatch = useDispatch();
  const User = useSelector(({ User }) => User);
  const Eth = useSelector(({ Eth }) => Eth);
  const Btc = useSelector(({ Btc }) => Btc);
  const Usdt = useSelector(({ Usdt }) => Usdt);
  const { currentUser } = User;
  const { currency, currencySymbol } = currentUser;
  useEffect(() => {
    dispatch(
      getETHPrices({
        currency,
      })
    );
    dispatch(
      getBTCPrices({
        currency,
      })
    );
    dispatch(
      getUSDTPrices({
        currency,
      })
    );
    dispatch(getUSDTBalance());
    dispatch(getETHBalance());
    dispatch(getBTCBalance());
  }, []);
  const usdtBalance = Usdt.balance;
  const usdtPriceNow = Usdt.priceNow;
  const totalUsdtBalance = usdtBalance * usdtPriceNow;
  const btcBalance = Btc.balance;
  const btcPriceNow = Btc.priceNow;
  const totalBtcBalance = btcBalance * btcPriceNow;
  const ethBalance = Eth.balance;
  const ethPriceNow = Eth.priceNow;
  const totalEthBalance = ethBalance * ethPriceNow;
  const totalBalance = totalUsdtBalance + totalBtcBalance + totalEthBalance;
  let formattedBalanceCurrency = parseFloat(totalBalance).toFixed(2);
  if (Number.isInteger(parseFloat(formattedBalanceCurrency))) {
    formattedBalanceCurrency = parseFloat(formattedBalanceCurrency).toString();
  }
  return (
    <Fragment>
      <div className="wallet-container">
        <div className="wallet-header-container blue">
          <div className="wallet-header-circle blue-circle" />
          <div className="wallet-header-content">
            <div className="wallet-header-text-row">
              <div className="wallet-header-text">Wallet</div>
              <div className="wallet-header-icon-container">
                <FontAwesomeIcon icon={faBell} />
              </div>
            </div>
            <div className="wallet-header-balance-row">
              <div className="wallet-header-dollar-icon-container">
                <img src={dollarImg} />
              </div>
              <div className="wallet-header-balance-container">
                <div className="wallet-header-balance-text">Wallet Balance</div>
                <div className="wallet-header-balance-value">
                  {formattedBalanceCurrency} {currency}
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="wallet-coins-container">
          <CoinItem
            options={options}
            coin="BTC"
            prices={Btc.prices}
            currencyDiff={Btc.currencyDiff}
            labels={Btc.labels}
            percentageDiff={Btc.percentageDiff}
            priceNow={Btc.priceNow}
            balance={Btc.balance}
          />
          <CoinItem
            options={options}
            coin="ETH"
            prices={Eth.prices}
            currencyDiff={Eth.currencyDiff}
            labels={Eth.labels}
            percentageDiff={Eth.percentageDiff}
            priceNow={Eth.priceNow}
            balance={Eth.balance}
          />
          <CoinItem
            options={options}
            coin="USDT"
            prices={Usdt.prices}
            currencyDiff={Usdt.currencyDiff}
            labels={Usdt.labels}
            percentageDiff={Usdt.percentageDiff}
            priceNow={Usdt.priceNow}
            balance={Usdt.balance}
          />
        </div>
      </div>
    </Fragment>
  );
};

export default withTabBar(Wallets);
