import React, { Fragment, useEffect } from "react";
import "../Languages/index.css";
import "./index.css";
import Select from "react-select";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronLeft, faPlus } from "@fortawesome/free-solid-svg-icons";
import currencies from "../../constants/currencies";
import Button from "../../shared/Button";
import withTabBar from "../../HOC/TabBar";
import ManageBankItem from "../../components/ManageBankItem";
import { useNavigate } from "react-router-dom";
import { getAllBanks } from "../../redux/actions/userAction";
import { useDispatch, useSelector } from "react-redux";

const ManageBanks = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const User = useSelector(({ User }) => User);
  const { banks } = User;
  useEffect(() => {
    dispatch(getAllBanks());
  }, []);
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div className="wallet-header-container light-blue coin-wallet-header">
          <div className="wallet-header-circle lightblue-circle" />
          <div className="wallet-header-content">
            <div className="preference-header-container">
              <div
                className="preference-icon-container"
                onClick={() => navigate("/settings")}
              >
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
              <div className="preference-header-text">Banks Settings</div>
            </div>
          </div>
        </div>
        <div className="coinwallet-body-container">
          <div className="bank-items-title">Manage Bank Accounts</div>
          <div
            className="manage-addbank-text"
            onClick={() => navigate("/addBank")}
          >
            <FontAwesomeIcon icon={faPlus} /> <span>Add Bank</span>
          </div>
          <div className="bank-items-container">
            {banks.map((bank) => (
              <ManageBankItem bank={bank} />
            ))}
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default ManageBanks;
