import React, { Fragment, useEffect } from "react";
import dollarImg from "../../assets/dollar.png";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBell,
  faChevronLeft,
  faExchange,
  faPaperPlane,
  faQrcode,
} from "@fortawesome/free-solid-svg-icons";
import Select from "react-select";
import CoinItemChart from "../../components/CoinItemChart";
import TransactionItem from "../../components/TransactionItem";
import QRCode from "react-qr-code";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { getBTCAddress } from "../../redux/actions/btcAction";
import { getETHAddress } from "../../redux/actions/ethAction";
import { getUSDTAddress } from "../../redux/actions/usdtAction";
import { toast } from "react-toastify";

const Recieve = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const User = useSelector(({ User }) => User);
  const Eth = useSelector(({ Eth }) => Eth);
  const Btc = useSelector(({ Btc }) => Btc);
  const Usdt = useSelector(({ Usdt }) => Usdt);
  const { selectedCoin } = User;
  useEffect(() => {
    if (selectedCoin === "BTC") {
      dispatch(getBTCAddress());
    } else if (selectedCoin === "ETH") {
      dispatch(getETHAddress());
    } else if (selectedCoin === "USDT") {
      dispatch(getUSDTAddress());
    }
  }, []);

  const coinMappings = {
    BTC: {
      coinName: "Bitcoin",
      circleColor: "yellow-circle",
      baseColor: "yellow",
      selectedReducer: Btc,
      address: Btc.address,
    },
    ETH: {
      coinName: "Ethereum",
      circleColor: "blue-circle",
      baseColor: "blue",
      selectedReducer: Eth,
      address: Eth.address,
    },
    USDT: {
      coinName: "Tether",
      circleColor: "green-circle",
      baseColor: "green",
      selectedReducer: Usdt,
      address: Usdt.address,
    },
  };

  const selectedCoinData = coinMappings[selectedCoin];

  const { coinName, circleColor, baseColor, selectedReducer, address } =
    selectedCoinData;
  const CopyToClipboard = () => {
    navigator.clipboard.writeText(address);
    toast.success('Address was copied to clipboard', {
      position: "top-right",
      autoClose: 2000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      progress: undefined,
      theme: "light",
      });
  };
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div
          className={`wallet-header-container ${baseColor} coin-wallet-header`}
        >
          <div className={`wallet-header-circle ${circleColor}`} />
          <div className="wallet-header-content">
            <div className="coinwallet-header-row">
              <div
                className="wallet-header-icon-container"
                onClick={() => navigate("/coinwallet")}
              >
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
              <div className="coinwallet-coin-name">{coinName}</div>
              <div className="wallet-header-icon-container">
                <FontAwesomeIcon icon={faBell} />
              </div>
            </div>
            <div className="coinwallet-balance">0 {selectedCoin}</div>
            <div className="coinwallet-balance-currency">0 {selectedCoin}</div>
            <div className="coinwallet-header-btn-container">
              <div className={`coinwallet-header-btn ${baseColor}`}>
                <div className="coinwallet-header-btn-icon-container">
                  <FontAwesomeIcon icon={faPaperPlane} />
                </div>
                <div className="coinwallet-header-btn-text">Send</div>
              </div>
              <div className={`coinwallet-header-btn ${baseColor}`}>
                <div className="coinwallet-header-btn-icon-container">
                  <FontAwesomeIcon icon={faQrcode} />
                </div>
                <div className="coinwallet-header-btn-text">Recieve</div>
              </div>
              <div className={`coinwallet-header-btn ${baseColor}`}>
                <div className="coinwallet-header-btn-icon-container">
                  <FontAwesomeIcon icon={faExchange} />
                </div>
                <div className="coinwallet-header-btn-text">Swap</div>
              </div>
            </div>
          </div>
        </div>
        <div className="coinwallet-body-container">
          <div className="line-container">
            <div className="line" />
          </div>
          <div className="qr-text-container">
            <div className="qr-title">QR code</div>
            <div className="qr-desc">Scan this for wallet address.</div>
          </div>
          <div className="qr-container">
            <QRCode value={address} />
          </div>
          <div className="recieve-wallet-address-text">Wallet Address</div>
          <div className="recieve-wallet-address-value">{address}</div>
          <div className="recieve-wallet-btn-container">
            <div
              className={`recieve-wallet-btn ${baseColor}`}
              onClick={CopyToClipboard}
            >
              Copy Address
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default Recieve;
