import React from "react";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTurnDown, faTurnUp } from "@fortawesome/free-solid-svg-icons";
import months from "../../constants/months";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setBtc } from "../../redux/actions/btcAction";
import { setETH } from "../../redux/actions/ethAction";
import { setUSDT } from "../../redux/actions/usdtAction";

const TransactionItem = ({ transaction, priceNow }) => {
  const { type, amount, date } = transaction;
  const transactionDate = new Date(date);
  const month = months[transactionDate.getMonth()];
  const day = transactionDate.getDate();
  const year = transactionDate.getFullYear();
  const formattedDateString = `${month} ${day}, ${year}`;

  const User = useSelector(({ User }) => User);
  const { selectedCoin, currentUser } = User;
  const { currency, currencySymbol } = currentUser;
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const onTransactionClick = () => {
    if (selectedCoin === "BTC") {
      dispatch(
        setBtc({
          name: "selectedTransaction",
          value: transaction,
        })
      );
    } else if (selectedCoin === "ETH") {
      dispatch(
        setETH({
          name: "selectedTransaction",
          value: transaction,
        })
      );
    } else if (selectedCoin === "USDT") {
      dispatch(
        setUSDT({
          name: "selectedTransaction",
          value: transaction,
        })
      );
    }
    navigate("/transactionDetails");
  };

  let balanceCurrency = priceNow * amount;
  let formattedBalanceCurrency = parseFloat(balanceCurrency).toFixed(0);
  if (Number.isInteger(parseFloat(formattedBalanceCurrency))) {
    formattedBalanceCurrency = parseFloat(formattedBalanceCurrency).toString();
  }

  return (
    <div className="transaction-item" onClick={onTransactionClick}>
      <div className="transaction-item-row">
        <div className="transaction-item-type-container">
          <div className="transaction-item-icon-container">
            <FontAwesomeIcon icon={faTurnUp} />
          </div>
          <div className="transaction-item-type-text">
            <div className="transaction-item-type-value">
              {type === "send" ? "Sent" : "Recieved"}
            </div>
            <div className="transaction-item-type-date">
              {formattedDateString}
            </div>
          </div>
        </div>
        <div className="transaction-item-amount-container">
          <div className="transaction-item-amount-value">{amount}</div>
          <div className="transaction-item-amount-currency">
            {currencySymbol}
            {formattedBalanceCurrency} {currency}
          </div>
        </div>
      </div>
    </div>
  );
};

export default TransactionItem;
