import React, { Fragment } from "react";
import dollarImg from "../../assets/dollar.png";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBell,
  faBitcoinSign,
  faChevronLeft,
  faExchange,
  faPaperPlane,
  faQrcode,
} from "@fortawesome/free-solid-svg-icons";
import Select from "react-select";
import CoinItemChart from "../../components/CoinItemChart";
import TransactionItem from "../../components/TransactionItem";
import QRCode from "react-qr-code";
import Input from "../../shared/Input";
import Button from "../../shared/Button";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { setUSDT } from "../../redux/actions/usdtAction";
import { setETH } from "../../redux/actions/ethAction";
import { setBtc } from "../../redux/actions/btcAction";
import { setUser } from "../../redux/actions/userAction";

const SendCoin = () => {
  const User = useSelector(({ User }) => User);
  const Eth = useSelector(({ Eth }) => Eth);
  const Btc = useSelector(({ Btc }) => Btc);
  const Usdt = useSelector(({ Usdt }) => Usdt);
  const { selectedCoin } = User;
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const coinMappings = {
    BTC: {
      coinName: "Bitcoin",
      circleColor: "yellow-circle",
      baseColor: "yellow",
      selectedReducer: Btc,
    },
    ETH: {
      coinName: "Ethereum",
      circleColor: "blue-circle",
      baseColor: "blue",
      selectedReducer: Eth,
    },
    USDT: {
      coinName: "Tether",
      circleColor: "green-circle",
      baseColor: "green",
      selectedReducer: Usdt,
    },
  };

  const selectedCoinData = coinMappings[selectedCoin];

  const { coinName, circleColor, baseColor, selectedReducer } =
    selectedCoinData;

  const { balance, sendAmount, recipientAddress } = selectedReducer;

  // Format Balance
  let formattedBalance = parseFloat(balance).toFixed(7);
  if (Number.isInteger(parseFloat(formattedBalance))) {
    formattedBalance = parseFloat(formattedBalance).toString();
  }
  const onChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    if (selectedCoin === "BTC") {
      dispatch(
        setBtc({
          name,
          value,
        })
      );
    } else if (selectedCoin === "ETH") {
      dispatch(
        setETH({
          name,
          value,
        })
      );
    } else if (selectedCoin === "USDT") {
      dispatch(
        setUSDT({
          name,
          value,
        })
      );
    }
  };
  const onContinue = (e) => {
    dispatch(
      setUser({
        name: "isSellTransaction",
        value: false,
      })
    );
    navigate("/reviewTransactions");
  };
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div
          className={`wallet-header-container ${baseColor} coin-wallet-header`}
        >
          <div className={`wallet-header-circle ${circleColor}`} />
          <div className="wallet-header-content">
            <div className="coinwallet-header-row">
              <div
                className="wallet-header-icon-container"
                onClick={() => navigate("/coinwallet")}
              >
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
              <div className="coinwallet-coin-name">{coinName}</div>
              <div className="wallet-header-icon-container">
                <FontAwesomeIcon icon={faBell} />
              </div>
            </div>
            <div className="coinwallet-balance">
              {formattedBalance} {selectedCoin}
            </div>
            <div className="coinwallet-balance-currency">$0 USD</div>
          </div>
        </div>
        <div className="coinwallet-body-container">
          <div className="line-container">
            <div className="line" />
          </div>
          <div className="sendcoin-container">
            <div className="auth-form-item">
              <div className="auth-label">Amount in USDT:</div>
              <Input
                type={"text"}
                className={"amount-input"}
                name={"sendAmount"}
                onChange={onChange}
                value={sendAmount}
              />
            </div>
            <div className="auth-form-item">
              <div className="auth-label">Recipient USDT Address:</div>
              <Input
                placeholder={"Enter Recipient Address"}
                type={"text"}
                name={"recipientAddress"}
                onChange={onChange}
                value={recipientAddress}
              />
            </div>
            <div className="auth-form-item">
              <Button
                className={baseColor}
                text={"Continue"}
                onClick={onContinue}
              />
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default SendCoin;
