import React, { Fragment, useState } from "react";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faCartShopping,
  faCog,
  faExchange,
  faHome,
  faUserAlt,
  faWallet,
} from "@fortawesome/free-solid-svg-icons";
import ExchangeImg from "../../assets/exchange.png";
import "./index.css";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setUser } from "../../redux/actions/userAction";

const withTabBar = (WrappedComponent) => {
  const TabBar = (props) => {
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const User = useSelector(({ User }) => User);
    const { currentTab } = User;
    const [selectedTab, setSelectedTab] = useState("/home");

    const onTabChange = (tabName) => {
      setSelectedTab("/wallets");
      navigate(tabName);
      dispatch(
        setUser({
          name: "currentTab",
          value: tabName,
        })
      );
    };

    return (
      <Fragment>
        <WrappedComponent {...props} />
        <div className="tabbar-space" />
        <div className="tabbar-container">
          <div
            className={`tab-item ${currentTab === "/home" && "selected"}`}
            onClick={() => onTabChange("/home")}
          >
            <div className="tab-icon-container">
              <FontAwesomeIcon icon={faHome} />
            </div>
            <div className="tab-title">Home</div>
          </div>
          <div
            className={`tab-item ${currentTab === "/wallets" && "selected"}`}
            onClick={() => onTabChange("/wallets")}
          >
            <div className="tab-icon-container">
              <FontAwesomeIcon icon={faWallet} />
            </div>
            <div className="tab-title">Wallet</div>
          </div>
          <div className="tab-item">
            <div className="exchange-img-container">
              <img src={ExchangeImg} className="exchange-img" />
            </div>
          </div>
          <div
            className={`tab-item ${currentTab === "/exchange" && "selected"}`}
            onClick={() => onTabChange("/exchange")}
          >
            <div className="tab-icon-container">
              <FontAwesomeIcon icon={faExchange} />
            </div>
            <div className="tab-title">Swap</div>
          </div>
          <div
            className={`tab-item ${currentTab === "/settings" && "selected"}`}
            onClick={() => onTabChange("/settings")}
          >
            <div className="tab-icon-container">
              <FontAwesomeIcon icon={faCog} />
            </div>
            <div className="tab-title">Settings</div>
          </div>
        </div>
      </Fragment>
    );
  };

  return TabBar;
};

export default withTabBar;
