import {
  faBuildingColumns,
  faCheck,
  faPencil,
} from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import "../BankItem/index.css";
import "./index.css";
import { useDispatch } from "react-redux";
import { setUser } from "../../redux/actions/userAction";
import { useNavigate } from "react-router-dom";

const ManageBankItem = ({ bank }) => {
  const { bankName, accountNumber } = bank;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const onClick = () => {
    dispatch(
      setUser({
        name: "editBank",
        value: bank,
      })
    );
    dispatch(
      setUser({
        name: "selectedBank",
        value: bank,
      })
    );
    navigate("/updateBank");
  };
  return (
    <div className="bank-item-container" onClick={onClick}>
      <div className="bank-item-content">
        <div className="bank-item-content-icon-container">
          <FontAwesomeIcon icon={faBuildingColumns} />
        </div>
        <div className="bank-item-text">
          <div className="bank-item-number">{accountNumber}</div>
          <div className="bank-item-name">{bankName}</div>
        </div>
      </div>
      <div className="bank-item-ischecked">
        <div className="bank-item-icon-container manage">
          <FontAwesomeIcon icon={faPencil} />
        </div>
        {/* <div className="bank-item-icon-container empty"></div> */}
      </div>
    </div>
  );
};

export default ManageBankItem;
