import React, { Fragment, useEffect } from "react";
import dollarImg from "../../assets/dollar.png";
import "../Wallets/index.css";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faArrowDown,
  faArrowRightArrowLeft,
  faBell,
  faCab,
  faCartArrowDown,
  faCog,
  faEye,
  faHandHoldingDollar,
  faPowerOff,
  faSignOut,
  faWallet,
} from "@fortawesome/free-solid-svg-icons";
import CoinItem from "../../components/CoinItem";
import withTabBar from "../../HOC/TabBar";
import Avatar from "../../assets/avatars/avatar4.png";
import Wave from "../../assets/wave.png";
import HomeWalletItem from "../../components/HomeWalletItem";
import { useDispatch, useSelector } from "react-redux";
import {
  getETHBalance,
  getETHPrices,
  getEthTransactions,
} from "../../redux/actions/ethAction";
import { logout } from "../../redux/actions/userAction";
import { useNavigate } from "react-router-dom";
import {
  getBTCBalance,
  getBTCPrices,
  getBtcTransactions,
} from "../../redux/actions/btcAction";
import {
  getUSDTBalance,
  getUSDTPrices,
  getUsdtTransactions,
} from "../../redux/actions/usdtAction";

const Home = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const User = useSelector(({ User }) => User);
  const Eth = useSelector(({ Eth }) => Eth);
  const Btc = useSelector(({ Btc }) => Btc);
  const Usdt = useSelector(({ Usdt }) => Usdt);
  const { selectedCoin, currentUser } = User;
  const { currency, currencySymbol } = currentUser;

  useEffect(() => {
    if (selectedCoin === "BTC") {
      dispatch(
        getETHPrices({
          currency,
        })
      );
      dispatch(getBTCBalance());
      dispatch(getBtcTransactions());
    } else if (selectedCoin === "ETH") {
      dispatch(
        getBTCPrices({
          currency,
        })
      );
      dispatch(getETHBalance());
      dispatch(getEthTransactions());
    } else if (selectedCoin === "USDT") {
      dispatch(
        getUSDTPrices({
          currency,
        })
      );
      dispatch(getUSDTBalance());
      dispatch(getUsdtTransactions());
    }
  }, []);


  const onLogout = () => {
    dispatch(logout({ navigate }));
  };
  const options = {
    scales: {
      x: {
        display: false, // Hide X axis labels
      },
      y: {
        display: false, // Hide Y axis labels
      },
    },
    plugins: {
      legend: {
        display: false, // Hide the legend
      },
    },
  };

  return (
    <Fragment>
      <div className="home-container">
        <div className="home-header-container">
          <div className="home-icon-container" onClick={onLogout}>
            <FontAwesomeIcon icon={faPowerOff} />
          </div>
          <div className="home-info-container">
            <img src={Avatar} className="home-info-image" />
            <div className="home-info-text-container">
              <div className="home-info-name">Good Morning, Abdullah Abid</div>
              <div className="home-info-balance">
                <span className="home-info-balance-text">
                  {" "}
                  Account Balance:
                </span>
                $200USD
                <div className="home-info-balance-container">
                  <FontAwesomeIcon icon={faEye} />
                </div>
              </div>
            </div>
          </div>
          <div className="home-pages-container">
            <div className="home-pages-row">
              <div className="home-pages-item">
                <div className="home-pages-item-icon-container">
                  <FontAwesomeIcon icon={faCartArrowDown} />
                </div>
                <div className="home-pages-item-text">Buy</div>
              </div>
              <div className="home-pages-item">
                <div className="home-pages-item-icon-container">
                  <FontAwesomeIcon icon={faHandHoldingDollar} />
                </div>
                <div className="home-pages-item-text">Sell</div>
              </div>
              <div className="home-pages-item">
                <div className="home-pages-item-icon-container">
                  <FontAwesomeIcon icon={faArrowDown} />
                </div>
                <div className="home-pages-item-text">Deposit</div>
              </div>
            </div>
            <div className="home-pages-row">
              <div className="home-pages-item">
                <div className="home-pages-item-icon-container">
                  <FontAwesomeIcon icon={faArrowRightArrowLeft} />
                </div>
                <div className="home-pages-item-text">Recieve</div>
              </div>
              <div className="home-pages-item">
                <div className="home-pages-item-icon-container">
                  <FontAwesomeIcon icon={faWallet} />
                </div>
                <div className="home-pages-item-text">Wallet</div>
              </div>
              <div className="home-pages-item">
                <div className="home-pages-item-icon-container">
                  <FontAwesomeIcon icon={faCog} />
                </div>
                <div className="home-pages-item-text">Settings</div>
              </div>
            </div>
          </div>
        </div>
        <div className="home-header-background">
          <img src={Wave} className="home-header-img" />
        </div>
        <div className="home-wallet-text">Wallets</div>
        <div className="wallet-coins-container">
          <HomeWalletItem
            options={options}
            coin="BTC"
            prices={Btc.prices}
            currencyDiff={Btc.currencyDiff}
            labels={Btc.labels}
            percentageDiff={Btc.percentageDiff}
            priceNow={Btc.priceNow}
            balance={Btc.balance}
          />
          <HomeWalletItem
            options={options}
            coin="ETH"
            prices={Eth.prices}
            currencyDiff={Eth.currencyDiff}
            labels={Eth.labels}
            percentageDiff={Eth.percentageDiff}
            priceNow={Eth.priceNow}
            balance={Eth.balance}
          />
          <HomeWalletItem
            options={options}
            coin="USDT"
            prices={Usdt.prices}
            currencyDiff={Usdt.currencyDiff}
            labels={Usdt.labels}
            percentageDiff={Usdt.percentageDiff}
            priceNow={Usdt.priceNow}
            balance={Usdt.balance}
          />
        </div>
        <div className="home-referal-container">
          <div className="home-referal-item">
            <div className="home-referal-item-text bold">
              invite friends and earn more
            </div>
            <div className="home-referal-item-text">refer and earn $100</div>
            <div className="home-referal-item-btn">invite friends</div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default withTabBar(Home);
