import {
  CLEAR_STATE,
  SET_ADD_BANK,
  SET_EDIT_BANK,
  SET_EDIT_PROFILE,
  SET_STATUS,
  SET_USER,
} from "../actions/types";
const initialState = {
  isLoading: false,
  email: "",
  password: "",
  currentUser: null,
  currentTab: "/home",
  selectedCoin: null,
  forgotPasswordEmail: "",
  emailVerificationDigits: ["", "", "", ""],
  loginPinDigits: ["", "", "", ""],
  confirmPinDigits: ["", "", "", ""],
  resetPassword: "",
  resetConfirmPassword: "",
  statusTitle: "",
  statusDesc: "",
  statusContinueRoute: "",
  editProfile: {
    name: "",
    email: "",
    phoneNumber: "",
    userName: "",
    country: "",
    image: "",
  },
  updateCurrentPin: ["", "", "", ""],
  updateNewPin: ["", "", "", ""],
  updateConfirmNewPin: ["", "", "", ""],
  currency: "",
  currencySymbol: "",
  banks: [],
  addBank: {
    accountHolder: "",
    bankName: "",
    accountNumber: "",
  },
  editBank: {
    accountHolder: "",
    bankName: "",
    accountNumber: "",
  },
  selectedBank: null,
  isExchangeBuy: true,
  currencyExchangeSelect: "",
  currencyExchange: "",
  coinExchangeSelect: "",
  coinExchange: "",
  coinExchangePrice: 0,
  isSellTransaction: false,
  settings: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case SET_USER: {
      return {
        ...state,
        [action.payload.name]: action.payload.value,
      };
    }
    case SET_STATUS: {
      const { statusTitle, statusDesc, statusContinueRoute } = action.payload;
      return {
        ...state,
        statusTitle,
        statusDesc,
        statusContinueRoute,
      };
    }
    case SET_EDIT_PROFILE: {
      return {
        ...state,
        editProfile: {
          ...state.editProfile,
          [action.payload.name]: action.payload.value,
        },
      };
    }
    case SET_ADD_BANK: {
      return {
        ...state,
        addBank: {
          ...state.addBank,
          [action.payload.name]: action.payload.value,
        },
      };
    }
    case SET_EDIT_BANK: {
      return {
        ...state,
        editBank: {
          ...state.editBank,
          [action.payload.name]: action.payload.value,
        },
      };
    }
    case CLEAR_STATE: {
      return initialState;
    }
    default:
      return state;
  }
};
