import React, { useEffect, useState } from "react";
import "./index.css";
import FourDigits from "../../shared/FourDigits";
import Button from "../../shared/Button";
import Avatar from "../../assets/avatars/avatar1.png";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { loginPin, setUser } from "../../redux/actions/userAction";
const LoginPin = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [image, setImage] = useState(null);
  const User = useSelector(({ User }) => User);
  const { loginPinDigits } = User;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const setDigits = (digits) => {
    dispatch(
      setUser({
        name: "loginPinDigits",
        value: digits,
      })
    );
  };
  useEffect(() => {
    const name = localStorage.getItem("name");
    const email = localStorage.getItem("email");
    const image = localStorage.getItem("image");
    setName(name);
    setEmail(email);
    if (image) setImage(image);
  }, []);

  const onClick = (e) => {
    const pin = loginPinDigits.join("");
    dispatch(
      loginPin({
        data: {
          email,
          pin,
        },
        navigate,
      })
    );
  };

  return (
    <div className="pin-container">
      <div className="transparent-circle" />
      <div className="pin-content">
        <div className="login-info-container">
          <img
            src={image ? require(`../../assets/avatars/${image}`) : Avatar}
          />
          <div className="login-info-name">{name}</div>
          <div className="login-info-email">{email}</div>
        </div>
        <div className="pin-inputs-container">
          <FourDigits digits={loginPinDigits} setDigits={setDigits} />
        </div>
        <div className="pin-btn-container">
          <Button text={"Login"} onClick={onClick} />
        </div>
        <div className="auth-alternate-link">
          <span> Login </span> with a different Account
        </div>
        <div className="auth-alternate-link">
          Login with <span>password</span>
        </div>
      </div>
    </div>
  );
};

export default LoginPin;
