import React, { Fragment } from "react";
import AuthImg from "../../assets/recover.png";
import Input from "../../shared/Input";
import "./index.css";
import Button from "../../shared/Button";
import { faEnvelope, faLock } from "@fortawesome/free-solid-svg-icons";
import { sendOTP, setUser } from "../../redux/actions/userAction";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";

const ForgotPassword = () => {
  const User = useSelector(({ User }) => User);
  const { forgotPasswordEmail } = User;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const onChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    dispatch(
      setUser({
        name,
        value,
      })
    );
  };

  const sendOtpOnEmail = (e) => {
    e.preventDefault();
    dispatch(
      sendOTP({
        data: {
          email: forgotPasswordEmail,
        },
        navigate,
      })
    );
  };
  return (
    <Fragment>
      <div className="auth-image-container forgotpass">
        <img src={AuthImg} className="forgotpass-image" />
      </div>
      <div className="forgotpass-content-container">
        <div className="forgotpass-text-container">
          <div className="forgotpass-title">Forget Password</div>
          <div className="forgotpass-desc">
            Enter the email address associated with your account
          </div>
        </div>
        <div className="auth-form-item">
          <div className="auth-label">Email address:</div>
          <Input
            placeholder={"Enter Email"}
            type={"text"}
            name={"forgotPasswordEmail"}
            icon={faEnvelope}
            onChange={onChange}
            value={forgotPasswordEmail}
          />
        </div>
        <div className="auth-form-item">
          <Button text={"Recover Password"} onClick={sendOtpOnEmail} />
        </div>
        <div className="auth-alternate-link"></div>
      </div>
    </Fragment>
  );
};

export default ForgotPassword;
