import React, { Fragment, useEffect, useState } from "react";
import USDTImg from "../../assets/usdt.png";
import BTCImg from "../../assets/bitcoin.png";
import ETHImg from "../../assets/ethereum.png";
import CoinItemChart from "../CoinItemChart";
import "./index.css";
import Chart from "../Chart";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { setUser } from "../../redux/actions/userAction";
import { formatNumberWithCommas } from "../../utils/helper";

const CoinItem = ({
  options,
  coin,
  prices,
  currencyDiff,
  labels,
  percentageDiff,
  priceNow,
  balance,
}) => {
  const User = useSelector(({ User }) => User);
  const { currentUser } = User;
  const { currency, currencySymbol } = currentUser;
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const coinItemImg =
    coin === "BTC" ? BTCImg : coin === "ETH" ? ETHImg : USDTImg;
  const borderColor =
    coin === "BTC"
      ? "rgba(244, 183, 63, 1)"
      : coin === "ETH"
      ? "rgba(0, 107, 250, 1)"
      : "rgba(75, 192, 192, 1)";
  const backgroundColor =
    coin === "BTC"
      ? "rgba(244, 183, 63, 0.2)"
      : coin === "ETH"
      ? "rgba(0, 107, 250, 0.2)"
      : "rgba(75, 192, 192, 0.2)";
  const parsedPercentageDiff = parseFloat(percentageDiff);
  const isPositive = !isNaN(parsedPercentageDiff) && parsedPercentageDiff >= 0;

  const onCoinClick = () => {
    dispatch(
      setUser({
        name: "selectedCoin",
        value: coin,
      })
    );
    navigate("/coinwallet");
  };
  // Format Balance
  let formattedBalance = parseFloat(balance).toFixed(7);
  if (Number.isInteger(parseFloat(formattedBalance))) {
    formattedBalance = parseFloat(formattedBalance).toString();
  }
  let balanceCurrency = priceNow * balance;
  let formattedBalanceCurrency = parseFloat(balanceCurrency).toFixed(2);
  if (Number.isInteger(parseFloat(formattedBalanceCurrency))) {
    formattedBalanceCurrency = parseFloat(formattedBalanceCurrency).toString();
  }
  return (
    <Fragment>
      <div className="coinitem-container" onClick={onCoinClick}>
        <div className="coinitem-stats-container">
          <div className="coinitem-img-container">
            <img src={coinItemImg} className="coinitem-img" />
          </div>
          <div className="coinitem-text-container">
            <div className="coinitem-text-title">{coin}</div>
            <div className="coinitem-text-value">
              {formattedBalance} {coin}
            </div>
            <div className="coinitem-text-value-currency">
              {currencySymbol}
              {formattedBalanceCurrency} {currency}
            </div>
          </div>
        </div>
        <div className="coinitem-chart-container">
          <div className="coinitem-chart-line" />
          <div className="coin-item-graph-container">
            <Chart
              labels={labels}
              prices={prices}
              backgroundColor={backgroundColor}
              borderColor={borderColor}
              fill={true}
              borderWidth={2}
              pointRadius={0}
              options={options}
            />
          </div>
          <div className="coin-item-current-value">
            {currencySymbol}
            {formatNumberWithCommas(priceNow)}
          </div>
          <div className="coin-item-diff-container">
            <div
              className={`coin-item-diff-value ${isPositive && "isPositive"}`}
            >
              {percentageDiff}
            </div>
            <div className="coin-item-diff-text">24hrs</div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default CoinItem;
