import React, { Fragment } from "react";
import resetImg from "../../assets/reset.png";
import Input from "../../shared/Input";
import "./index.css";
import Button from "../../shared/Button";
import { faEnvelope, faLock } from "@fortawesome/free-solid-svg-icons";
import { useDispatch, useSelector } from "react-redux";
import { resetUserPassword, setUser } from "../../redux/actions/userAction";
import Swal from "sweetalert2";
import { useNavigate } from "react-router-dom";
const NewPassword = () => {
  const User = useSelector(({ User }) => User);
  const {
    resetPassword,
    forgotPasswordEmail,
    emailVerificationDigits,
    resetConfirmPassword,
  } = User;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const onChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    dispatch(
      setUser({
        name,
        value,
      })
    );
  };
  const onClick = () => {
    if (resetPassword !== resetConfirmPassword) {
      Swal.fire({
        icon: "error",
        title: "Recheck Passwords",
        text: "Password and Confirm password are not same",
      });
      return;
    }

    const email = forgotPasswordEmail;
    const otp = parseInt(emailVerificationDigits.join(""), 10);
    const password = resetPassword;
    dispatch(
      resetUserPassword({
        data: {
          email,
          otp,
          password,
        },
        navigate,
      })
    );
  };
  return (
    <Fragment>
      <div className="container">
        <div className="auth-image-container">
          <img src={resetImg} className="new-password-image" />
        </div>
        <div className="auth-content-container">
          <div className="new-password-header">Create New Password</div>
          <div className="new-password-desc">
            Your new password must be different from previously used password.
          </div>
          <div className="auth-form-item">
            <div className="auth-label">New Password:</div>
            <Input
              placeholder={"Enter Password"}
              type={"password"}
              icon={faLock}
              showPassIcon={true}
              name={"resetPassword"}
              value={resetPassword}
              onChange={onChange}
            />
          </div>
          <div className="auth-form-item">
            <div className="auth-label">Confirm New Password:</div>
            <Input
              placeholder={"Enter Password"}
              type={"password"}
              icon={faLock}
              showPassIcon={true}
              name={"resetConfirmPassword"}
              value={resetConfirmPassword}
              onChange={onChange}
            />
          </div>
          <div className="new-password-container">
            <Button text={"Update Password"} onClick={onClick} />
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default NewPassword;
