import React from "react";
import MainImg from "../../assets/main.png";
import "./index.css";
import Button from "../../shared/Button";
import { useNavigate } from "react-router-dom";

const Main = () => {
  const navigate = useNavigate();
  const onLoginClick = (route) => {
    const hasPin = localStorage.getItem("hasPin");
    if (hasPin) {
      navigate("/loginPin");
    } else {
      navigate("login");
    }
  };
  return (
    <div className="main-container">
      <div className="main-content">
        <div className="main-content-title">Coinatik</div>
        <img src={MainImg} className="main-content-img" />
      </div>
      <div className="main-desc">Best way to invest your money</div>
      <div className="main-btn-container">
        <Button
          text={"Sign Up"}
          className={"main-btn"}
          onClick={() => navigate("/register")}
        />
        <Button
          text={"Sign In"}
          className={"main-btn grey"}
          onClick={() => onLoginClick("/login")}
        />
      </div>
    </div>
  );
};

export default Main;
