import { CLEAR_STATE, SET_BTC, SET_BTC_CHART } from "../actions/types";
const initialState = {
  prices: [],
  currencyDiff: "",
  labels: [],
  percentageDiff: "",
  priceNow: 0,
  address: "",
  balance: 0,
  balanceCurrency: 0,
  sendAmount: 0,
  recipientAddress: "",
  gasPrice: "",
  currentTransactionPage: 1,
  transactions: [],
  paginatedTransactions: [],
  totalTransactionPages: 1,
  transactionsPerPage: 5,
  selectedTransaction: null,
};

export default (state = initialState, action) => {
  switch (action.type) {
    case SET_BTC: {
      return {
        ...state,
        [action.payload.name]: action.payload.value,
      };
    }
    case SET_BTC_CHART: {
      const { labels, prices, currencyDiff, percentageDiff, priceNow } =
        action.payload;
      return {
        ...state,
        labels,
        prices,
        currencyDiff,
        percentageDiff,
        priceNow,
      };
    }
    case CLEAR_STATE: {
      return initialState;
    }
    default:
      return state;
  }
};
