import {
  BASE_URL,
  CLEAR_STATE,
  SET_ADD_BANK,
  SET_EDIT_BANK,
  SET_EDIT_PROFILE,
  SET_STATUS,
  SET_USER,
} from "./types";
import Axios from "axios";
import Swal from "sweetalert2";
import { toast } from "react-toastify";

// Set User
export const setUser = (data) => async (dispatch) => {
  try {
    dispatch({ type: SET_USER, payload: data });
  } catch (error) {
    console.log(error);
  }
};
