// Set Loader
export const setLoader = (isLoading) => async (dispatch) => {
  try {
    dispatch({
      type: SET_USER,
      payload: {
        name: "isLoading",
        value: isLoading,
      },
    });
  } catch (error) {
    console.log(error);
  }
};
// Login a User
export const login =
  ({ data, navigate }) =>
  async (dispatch) => {
    try {
      const resp = await Axios.post(`${BASE_URL}/api/user/login`, data, {
        withCredentials: true,
      });
      const currentUser = resp.data.user;
      console.log(currentUser);
      dispatch(setUser({ name: "currentUser", value: currentUser }));
      dispatch(getSettings());
      const hasPin = Boolean(currentUser.pin);
      if (hasPin) localStorage.setItem("hasPin", true);
      localStorage.setItem("name", currentUser.name);
      localStorage.setItem("email", currentUser.email);
      if (currentUser.image) localStorage.setItem("image", currentUser.image);
      navigate("/home");
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Error Occured",
        text: "Invalid Credentials",
      });
      console.log(error);
    }
  };
export const getSettings = () => async (dispatch) => {
  try {
    const resp = await Axios.get(`${BASE_URL}/api/user/settings`, {
      withCredentials: true,
    });
    const settings = resp.data.settings;
    dispatch(setUser({ name: "settings", value: settings }));
  } catch (error) {
    console.log(error);
  }
};

