// Send OTP to the User
export const sendOTP =
  ({ data, navigate }) =>
  async (dispatch) => {
    dispatch(setLoader(true));
    try {
      await Axios.post(`${BASE_URL}/api/user/sendOTP`, data);
      navigate("/emailVerification");
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Error Occured",
        text: "Could not not Send OTP",
      });
      console.log(error);
    }
    dispatch(setLoader(false));
  };

// Send OTP to the User
export const checkOTP =
  ({ data, navigate }) =>
  async (dispatch) => {
    dispatch(setLoader(true));
    try {
      const resp = await Axios.post(`${BASE_URL}/api/user/checkOTP`, data);
      const isCorrectOTP = resp.data.isCorrectOTP;
      if (isCorrectOTP) {
        navigate("/resetPassword");
      } else {
        Swal.fire({
          icon: "error",
          title: "Invalid OTP",
          text: "The Entired OTP is not correct",
        });
      }
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Error Occured",
        text: "Could not check OTP",
      });
      console.log(error);
    }
    dispatch(setLoader(false));
  };

// Reset a Password
export const resetUserPassword =
  ({ data, navigate }) =>
  async (dispatch) => {
    dispatch(setLoader(true));
    try {
      await Axios.post(`${BASE_URL}/api/user/forgotpassword`, data);
      dispatch({
        type: SET_STATUS,
        payload: {
          statusTitle: "Success",
          statusDesc: "Your Password was change successfully",
          statusContinueRoute: "/login",
        },
      });
      navigate("/status");
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Error Occured",
        text: "Could not check OTP",
      });
      console.log(error);
    }
    dispatch(setLoader(false));
  };

