// Update a Profile
export const updateProfile =
  ({ data, navigate }) =>
  async (dispatch) => {
    dispatch(setLoader(true));
    try {
      const resp = await Axios.post(
        `${BASE_URL}/api/user/updateProfile`,
        data,
        {
          withCredentials: true,
        }
      );
      const user = resp.data.user;
      dispatch(
        setUser({
          name: "currentUser",
          value: user,
        })
      );
      toast.success("Profile Updated", {
        position: "top-right",
        autoClose: 2000,
        hideProgressBar: false,
        closeOnClick: true,
        pauseOnHover: true,
        progress: undefined,
        theme: "light",
      });
      navigate("/profile");
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Error Occured",
        text: "",
      });
      console.log(error);
    }
    dispatch(setLoader(false));
  };

