// Buy Crypto
export const buyCrypto =
  ({ data, navigate }) =>
  async (dispatch) => {
    dispatch(setLoader(true));
    try {
      let coin = data.coin;
      coin = coin.toLowerCase();
      await Axios.post(`${BASE_URL}/api/${coin}/buy`, data, {
        withCredentials: true,
      });
      dispatch({
        type: SET_STATUS,
        payload: {
          statusTitle: "Pending",
          statusDesc:
            "Order was placed Successfully, the Admin will check and release the funds,please wait..",
          statusContinueRoute: "/exchange",
        },
      });
      navigate("/status");
    } catch (error) {
      Swal.fire({
        icon: "error",
        title: "Error Occured",
        text: error.message,
      });
      console.log(error);
    }
    dispatch(setLoader(false));
  };

