import { faBuildingColumns, faCheck } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import "./index.css";
import { useDispatch, useSelector } from "react-redux";
import { setUser } from "../../redux/actions/userAction";
import { useNavigate } from "react-router-dom";

const BankItem = ({ bank, isSelected }) => {
  const { bankName, accountNumber } = bank;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const onClick = () => {
    dispatch(
      setUser({
        name: "selectedBank",
        value: bank,
      })
    );
    navigate("/exchange");
  };
  return (
    <div
      className={`bank-item-container  ${isSelected && "isSelected"}`}
      onClick={onClick}
    >
      <div className="bank-item-content">
        <div className="bank-item-content-icon-container">
          <FontAwesomeIcon icon={faBuildingColumns} />
        </div>
        <div className="bank-item-text">
          <div className="bank-item-number">{accountNumber}</div>
          <div className="bank-item-name">{bankName}</div>
        </div>
      </div>
      <div className={`bank-item-ischecked`}>
        <div className="bank-item-icon-container">
          <FontAwesomeIcon icon={faCheck} />
        </div>
        <div className="bank-item-icon-container empty"></div>
      </div>
    </div>
  );
};

export default BankItem;
