import React, { Fragment } from "react";
import "../../pages/Languages/index.css";
import "../../pages/AddBank/index.css";
import "./index.css";
import Select from "react-select";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronLeft } from "@fortawesome/free-solid-svg-icons";
import languages from "../../constants/languages";
import Button from "../../shared/Button";
import withTabBar from "../../HOC/TabBar";
import Input from "../../shared/Input";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import {
  editUserBank,
  removeUserBank,
  setEditBank,
} from "../../redux/actions/userAction";
import Swal from "sweetalert2";

const UpdateBank = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const User = useSelector(({ User }) => User);
  const { editBank, selectedBank } = User;
  const { accountHolder, bankName, accountNumber } = editBank;

  const onChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    dispatch(
      setEditBank({
        name,
        value,
      })
    );
  };
  const onUpdate = () => {
    const bankId = selectedBank._id;
    dispatch(
      editUserBank({
        data: { bankId, accountHolder, bankName, accountNumber },
        navigate,
      })
    );
  };
  const onDelete = async () => {
    try {
      const result = await Swal.fire({
        title: "Do you want to proceed?",
        text: "The Bank will be removed permenantly!",
        icon: "warning",
        showCancelButton: true,
        confirmButtonText: "Yes",
        cancelButtonText: "No",
        confirmButtonColor: "#3085d6",
        cancelButtonColor: "#d33",
      });

      if (result.isConfirmed) {
        const bankId = selectedBank._id;
        dispatch(
          removeUserBank({
            data: { bankId },
            navigate,
          })
        );
      }
    } catch (error) {
      console.error(error);
    }
  };
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div className="wallet-header-container light-blue coin-wallet-header">
          <div className="wallet-header-circle lightblue-circle" />
          <div className="wallet-header-content">
            <div className="preference-header-container">
              <div className="preference-icon-container">
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
              <div className="preference-header-text">Update Bank</div>
            </div>
          </div>
        </div>
        <div className="coinwallet-body-container">
          <div className="preference-content-container">
            <div className="auth-form-item">
              <div className="auth-label">Account Holder Name:</div>
              <Input
                placeholder={"Enter Account Holder Name"}
                type={"text"}
                onChange={onChange}
                name={"accountHolder"}
                value={accountHolder}
              />
            </div>
            <div className="auth-form-item">
              <div className="auth-label">Bank Name:</div>
              <Input
                placeholder={"Enter Bank Name"}
                type={"text"}
                onChange={onChange}
                name={"bankName"}
                value={bankName}
              />
            </div>
            <div className="auth-form-item">
              <div className="auth-label">Account Number:</div>
              <Input
                placeholder={"Enter Account Number"}
                type={"text"}
                onChange={onChange}
                name={"accountNumber"}
                value={accountNumber}
              />
            </div>
            <div className="preference-btn-row">
              <div className="preference-btn-item">
                <Button text={"Delete"} className={"red"} onClick={onDelete} />
              </div>
              <div className="preference-btn-item">
                <Button text={"Update"} onClick={onUpdate} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default withTabBar(UpdateBank);
