import { faCheck } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import React from "react";
import "./index.css";
import { useNavigate } from "react-router-dom";
import { useSelector } from "react-redux";
const Status = () => {
  const User = useSelector(({ User }) => User);
  const { statusContinueRoute, statusDesc, statusTitle } = User;
  const navigate = useNavigate();
  const onClick = () => {
    navigate(statusContinueRoute);
  };
  return (
    <div className="transfer-status">
      <div className="transfer-status-icon-container">
        <FontAwesomeIcon icon={faCheck} />
      </div>
      <div className="transfer-status-header">{statusTitle}</div>
      <div className="transfer-status-desc">{statusDesc}</div>
      <div className="transfer-status-btn-container">
        <div className="transfer-status-btn" onClick={onClick}>
          Continue
        </div>
      </div>
    </div>
  );
};

export default Status;
