import React, { Fragment } from "react";
import USDTImg from "../../assets/usdt.png";
import BTCImg from "../../assets/bitcoin.png";
import ETHImg from "../../assets/ethereum.png";
import CoinItemChart from "../CoinItemChart";
import "../CoinItem/index.css";
import "./index.css";
import Chart from "../Chart";
import { formatNumberWithCommas } from "../../utils/helper";
import { useSelector } from "react-redux";

const HomeWalletItem = ({
  options,
  coin,
  prices,
  currencyDiff,
  labels,
  percentageDiff,
  priceNow,
  balance,
}) => {
  const homewalletImg =
    coin === "BTC" ? BTCImg : coin === "ETH" ? ETHImg : USDTImg;
  const parsedPercentageDiff = parseFloat(percentageDiff);
  const isPositive = !isNaN(parsedPercentageDiff) && parsedPercentageDiff >= 0;
  const chartColor = isPositive ? "#6CBC3B" : "#EA333E";
  let formattedBalance = parseFloat(balance).toFixed(3);
  if (Number.isInteger(parseFloat(formattedBalance))) {
    formattedBalance = parseFloat(formattedBalance).toString();
  }
  const User = useSelector(({ User }) => User);
  const { currentUser } = User;
  const { currency, currencySymbol } = currentUser;

  return (
    <Fragment>
      <div className="homeWalletItem-container">
        <div className="homeWalletItem-img-container">
          <img src={homewalletImg} className="homeWalletItem-img" />
        </div>
        <div className="homeWalletItem-coins-container">
          <div className="homeWalletItem-coins-title">{coin}</div>
          <div className="homeWalletItem-coins-value">
            {formattedBalance} {coin}
          </div>
        </div>
        <div className="coinitem-chart-container">
          <div className="coin-item-graph-container">
            <Chart
              labels={labels}
              prices={prices}
              backgroundColor={chartColor}
              borderColor={chartColor}
              fill={false}
              borderWidth={1}
              pointRadius={0}
              options={options}
            />
          </div>
        </div>
        <div className="homeWalletItem-price-container">
          <div className="homeWalletItem-price-coin">
            {currencySymbol}
            {formatNumberWithCommas(priceNow)}
          </div>
          <div className="homeWalletItem-price-variation">
            {currencySymbol}
            {currencyDiff}{" "}
            <span
              className={`homeWalletItem-price-percentage ${
                isPositive && "isPositive"
              }`}
            >
              {" "}
              {percentageDiff}
            </span>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default HomeWalletItem;
