import React, { Fragment } from "react";
import "../Languages/index.css";
import Select from "react-select";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronLeft } from "@fortawesome/free-solid-svg-icons";
import currencies from "../../constants/currencies";
import Button from "../../shared/Button";
import withTabBar from "../../HOC/TabBar";
import { useDispatch, useSelector } from "react-redux";
import { setUser, updateProfile } from "../../redux/actions/userAction";
import { useNavigate } from "react-router-dom";
const Currency = () => {
  const User = useSelector(({ User }) => User);
  const { currency } = User;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const onChange = (selectedCurrency) => {
    dispatch(
      setUser({
        name: "currency",
        value: selectedCurrency,
      })
    );
  };
  const onSubmit = () => {
    const _currency = currency.value;
    const symbol = currency.symbol;
    dispatch(
      updateProfile({
        data: {
          currency: _currency,
          currencySymbol: symbol,
        },
        navigate,
      })
    );
  };
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div className="wallet-header-container light-blue coin-wallet-header">
          <div className="wallet-header-circle lightblue-circle" />
          <div className="wallet-header-content">
            <div className="preference-header-container">
              <div className="preference-icon-container">
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
              <div className="preference-header-text">Currency</div>
            </div>
          </div>
        </div>
        <div className="coinwallet-body-container">
          <div className="preference-content-container">
            <div className="auth-form-item">
              <div className="auth-label">Choose Currency:</div>
              <Select
                options={currencies}
                placeholder={"Select Currency"}
                onChange={onChange}
                value={currency}
              />
            </div>
            <div className="preference-btn-row">
              <div className="preference-btn-item">
                <Button
                  text={"Cancel"}
                  className={"red"}
                  onClick={() => navigate("/settings")}
                />
              </div>
              <div className="preference-btn-item">
                <Button text={"Change"} onClick={onSubmit} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default withTabBar(Currency);
