import { useState } from "react";
import { UserData } from "../../Data";
import { Line } from "react-chartjs-2";

function CoinItemChart({
  options,
  backgroundColor,
  borderColor,
  prices,
  labels,
}) {
  const [userData, setUserData] = useState({
    labels: labels,
    datasets: [
      {
        label: "Coin Price",
        data: prices,
        fill: true, // Set to true for area chart
        backgroundColor, // Color of the filled area
        borderColor,
        borderWidth: 2,
      },
    ],
  });

  return (
    <div style={{ width: "100%" }}>
      <Line data={userData} options={options} />
    </div>
  );
}

export default CoinItemChart;
