import React from "react";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faCheck, faPencil } from "@fortawesome/free-solid-svg-icons";
import Button from "../../shared/Button";
import withTabBar from "../../HOC/TabBar";
import { useDispatch, useSelector } from "react-redux";
import Avatar from "../../assets/avatars/avatar1.png";
import { useNavigate } from "react-router-dom";
import { setUser } from "../../redux/actions/userAction";
import countries from "../../constants/countries";

const Profile = () => {
  const User = useSelector(({ User }) => User);
  const { currentUser } = User;
  const { name, email, image, phoneNumber, userName, country, date } =
    currentUser;
  const options = { year: "numeric", month: "long" };
  const formattedDate = new Date(date).toLocaleString("en-US", options);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const onClick = () => {
    const selectedCountry = countries.find(
      (countryItem) => countryItem.value === country
    );
    dispatch(
      setUser({
        name: "editProfile",
        value: {
          name,
          email,
          phoneNumber,
          userName,
          country: selectedCountry,
          image,
        },
      })
    );
    navigate("/editProfile");
  };
  return (
    <div className="profile-container">
      <div className="transparent-circle" />
      <div className="profile-content">
        {" "}
        <div className="profile-title">
          <div className="profile-text">Profile</div>
          <div className="profile-icon-container">
            <FontAwesomeIcon icon={faPencil} />
          </div>
        </div>
        <div className="profile-header">
          <div className="profile-image-container">
            <img
              src={image ? require(`../../assets/avatars/${image}`) : Avatar}
              className="profile-image"
            />
            <div className="profile-image-active" />
          </div>
          <div className="profile-name">
            <div className="profile-name-text">{name}</div>
            <div className="profile-name-icon-container">
              <FontAwesomeIcon icon={faCheck} />
            </div>
          </div>
          <div className="profile-username">@{userName}</div>
        </div>
        <div className="profile-body">
          <div className="profile-body-title">Personal Info</div>
          <div className="profile-detail-list">
            <div className="profile-detail-item">
              <div className="profile-detail-header">Full Name</div>
              <div className="profile-detail-value">{name}</div>
            </div>

            <div className="profile-detail-item">
              <div className="profile-detail-header">Username</div>
              <div className="profile-detail-value">{userName}</div>
            </div>
            <div className="profile-detail-item">
              <div className="profile-detail-header">Email</div>
              <div className="profile-detail-value">{email}</div>
            </div>
            <div className="profile-detail-item">
              <div className="profile-detail-header">Phone Number</div>
              <div className="profile-detail-value">{phoneNumber}</div>
            </div>
            <div className="profile-detail-item">
              <div className="profile-detail-header">Country</div>
              <div className="profile-detail-value">{country}</div>
            </div>
            <div className="profile-detail-item">
              <div className="profile-detail-header">Joined</div>
              <div className="profile-detail-value">{formattedDate}</div>
            </div>
          </div>
          <div className="profile-body-container">
            <Button text={"Edit Profile"} onClick={onClick} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default withTabBar(Profile);
