import React, { useState } from "react";
import "../Profile/index.css";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faAt,
  faCheck,
  faEnvelope,
  faPencil,
  faPhone,
  faUser,
} from "@fortawesome/free-solid-svg-icons";
import Avatar from "../../assets/avatars/avatar4.png";
import Avatar1 from "../../assets/avatars/avatar1.png";
import Avatar2 from "../../assets/avatars/avatar2.png";
import Avatar3 from "../../assets/avatars/avatar3.png";
import Avatar4 from "../../assets/avatars/avatar4.png";
import Avatar5 from "../../assets/avatars/avatar5.png";
import Avatar6 from "../../assets/avatars/avatar6.png";
import Avatar7 from "../../assets/avatars/avatar7.png";
import Avatar8 from "../../assets/avatars/avatar8.png";
import Button from "../../shared/Button";
import withTabBar from "../../HOC/TabBar";
import Input from "../../shared/Input";
import Select from "react-select";
import countries from "../../constants/countries";
import { useDispatch, useSelector } from "react-redux";
import { setEditProfile, updateProfile } from "../../redux/actions/userAction";
import { useNavigate } from "react-router-dom";

const EditProfile = () => {
  const User = useSelector(({ User }) => User);
  const { editProfile } = User;
  const { name, email, phoneNumber, userName, country, image } = editProfile;
  const [selectedImage, setselectedImage] = useState(
    image ? image : "avatar1.png"
  );
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const [toggleAvatars, setToggleAvatars] = useState(false);
  const onChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    dispatch(
      setEditProfile({
        name,
        value,
      })
    );
  };
  const onToggle = () => {
    setToggleAvatars(!toggleAvatars);
  };
  const onUpdate = () => {
    const selectedCountry = country.value;
    dispatch(
      updateProfile({
        data: {
          name,
          phoneNumber,
          userName,
          country: selectedCountry,
          image: selectedImage,
        },
        navigate,
      })
    );
  };
  return (
    <div className="profile-container">
      <div className="transparent-circle" />
      <div className="profile-content">
        <div className="profile-title">
          <div className="profile-text">Profile</div>
          <div className="profile-icon-container">
            <FontAwesomeIcon icon={faPencil} />
          </div>
        </div>
        <div className="profile-header">
          <div className="profile-image-container">
            <img
              src={require(`../../assets/avatars/${selectedImage}`)}
              className="profile-image"
            />
            <div
              className="profile-image-edit-icon-container"
              onClick={onToggle}
            >
              <FontAwesomeIcon icon={faPencil} />
            </div>
          </div>
          {toggleAvatars && (
            <div className="profile-avatar-list">
              <div className="profile-avatar-row">
                <div
                  className={`profile-avatar-item ${
                    selectedImage === "avatar1.png" && "selected"
                  } `}
                  onClick={() => setselectedImage("avatar1.png")}
                >
                  <img src={Avatar1} className="profile-avatar-item-img" />
                </div>
                <div
                  className={`profile-avatar-item ${
                    selectedImage === "avatar2.png" && "selected"
                  } `}
                  onClick={() => setselectedImage("avatar2.png")}
                >
                  <img src={Avatar2} className="profile-avatar-item-img" />
                </div>
                <div
                  className={`profile-avatar-item ${
                    selectedImage === "avatar3.png" && "selected"
                  } `}
                  onClick={() => setselectedImage("avatar3.png")}
                >
                  <img src={Avatar3} className="profile-avatar-item-img" />
                </div>
                <div
                  className={`profile-avatar-item ${
                    selectedImage === "avatar4.png" && "selected"
                  } `}
                  onClick={() => setselectedImage("avatar4.png")}
                >
                  <img src={Avatar4} className="profile-avatar-item-img" />
                </div>
              </div>
              <div className="profile-avatar-row">
                <div
                  className={`profile-avatar-item ${
                    selectedImage === "avatar5.png" && "selected"
                  } `}
                  onClick={() => setselectedImage("avatar5.png")}
                >
                  <img src={Avatar5} className="profile-avatar-item-img" />
                </div>
                <div
                  className={`profile-avatar-item ${
                    selectedImage === "avatar6.png" && "selected"
                  } `}
                  onClick={() => setselectedImage("avatar6.png")}
                >
                  <img src={Avatar6} className="profile-avatar-item-img" />
                </div>
                <div
                  className={`profile-avatar-item ${
                    selectedImage === "avatar7.png" && "selected"
                  } `}
                  onClick={() => setselectedImage("avatar7.png")}
                >
                  <img src={Avatar7} className="profile-avatar-item-img" />
                </div>
                <div
                  className={`profile-avatar-item ${
                    selectedImage === "avatar8.png" && "selected"
                  } `}
                  onClick={() => setselectedImage("avatar8.png")}
                >
                  <img src={Avatar8} className="profile-avatar-item-img" />
                </div>
              </div>
            </div>
          )}
          <div className="profile-name">
            <div className="profile-name-text">{name}</div>
            <div className="profile-name-icon-container">
              <FontAwesomeIcon icon={faCheck} />
            </div>
          </div>
          <div className="profile-username">@{userName}</div>
        </div>
        <div className="profile-body">
          <div className="editprofile-form">
            <div className="editprofile-title">
              Edit your personal information
            </div>
            <div className="auth-form-item">
              <div className="auth-label">Full Name:</div>
              <Input
                placeholder={"Enter Full Name"}
                icon={faUser}
                type={"text"}
                name={"name"}
                onChange={onChange}
                value={name}
              />
            </div>
            <div className="auth-form-item">
              <div className="auth-label">UserName:</div>
              <Input
                placeholder={"Enter Username"}
                icon={faAt}
                type={"text"}
                name={"userName"}
                onChange={onChange}
                value={userName}
              />
            </div>
            <div className="auth-form-item">
              <div className="auth-label">Email:</div>
              <Input
                placeholder={"Enter Email"}
                icon={faEnvelope}
                type={"text"}
                name={"email"}
                value={email}
                readOnly={true}
              />
            </div>
            <div className="auth-form-item">
              <div className="auth-label">Country:</div>
              <Select options={countries} value={country} />
            </div>
            <div className="auth-form-item">
              <div className="auth-label">Phone Number:</div>
              <Input
                placeholder={"Enter Phone Number"}
                icon={faPhone}
                type={"text"}
                name={"phoneNumber"}
                onChange={onChange}
                value={phoneNumber}
              />
            </div>
          </div>
          <div className="profile-body-container">
            <Button text={"Save Changes"} onClick={onUpdate} />
          </div>
        </div>
      </div>
    </div>
  );
};

export default withTabBar(EditProfile);
