import React, { useEffect, useState } from "react";
import "../LoginPin/index.css";
import "./index.css";
import FourDigits from "../../shared/FourDigits";
import Button from "../../shared/Button";
import Avatar from "../../assets/avatars/avatar1.png";
import { useDispatch, useSelector } from "react-redux";
import { useNavigate } from "react-router-dom";
import { loginPin, sellCrypto, setUser } from "../../redux/actions/userAction";
import { sendBTC } from "../../redux/actions/btcAction";
import { sendETH } from "../../redux/actions/ethAction";
import { sendUSDT } from "../../redux/actions/usdtAction";
const ConfirmPin = () => {
  const [name, setName] = useState("");
  const [email, setEmail] = useState("");
  const [image, setImage] = useState(null);
  const User = useSelector(({ User }) => User);
  const Eth = useSelector(({ Eth }) => Eth);
  const Btc = useSelector(({ Btc }) => Btc);
  const Usdt = useSelector(({ Usdt }) => Usdt);
  const {
    confirmPinDigits,
    currentUser,
    selectedCoin,
    selectedBank,
    isSellTransaction,
    coinExchange,
    currencyExchangeSelect,
  } = User;
  const coinMappings = {
    BTC: {
      selectedReducer: Btc,
    },
    ETH: {
      selectedReducer: Eth,
    },
    USDT: {
      selectedReducer: Usdt,
    },
  };
  const selectedCoinData = coinMappings[selectedCoin];

  const { selectedReducer } = selectedCoinData;

  const { sendAmount, recipientAddress, gasPrice } = selectedReducer;

  const transactionFeeCoin = selectedCoin === "USDT" ? "ETH" : selectedCoin;
  const totalAmount =
    selectedCoin === "USDT"
      ? sendAmount
      : (parseFloat(gasPrice) + parseFloat(sendAmount)).toFixed(7);

  const dispatch = useDispatch();
  const navigate = useNavigate();
  const setDigits = (digits) => {
    dispatch(
      setUser({
        name: "confirmPinDigits",
        value: digits,
      })
    );
  };
  useEffect(() => {
    const name = currentUser?.name;
    const email = currentUser?.email;
    const image = currentUser?.image;
    setName(name);
    setEmail(email);
    if (image) setImage(image);
  }, []);

  const onClick = (e) => {
    const pin = confirmPinDigits.join("");

    if (isSellTransaction) {
      const currency = currencyExchangeSelect.value;
      const { accountHolder, bankName, accountNumber } = selectedBank;
      const data = {
        pin,
        accountHolder,
        bankName,
        accountNumber,
        currency,
        coin: selectedCoin,
        amount: parseFloat(coinExchange),
      };
      dispatch(sellCrypto({ data: data, navigate }));
    } else {
      if (selectedCoin === "BTC") {
        dispatch(
          sendBTC({
            data: {
              pin,
              amount: sendAmount,
              recipientAddress,
            },
            navigate,
          })
        );
      } else if (selectedCoin === "ETH") {
        console.log({
          pin,
          amount: sendAmount,
          recipientAddress,
        });
        dispatch(
          sendETH({
            data: {
              pin,
              amount: sendAmount,
              recipientAddress,
            },
            navigate,
          })
        );
      } else if (selectedCoin === "USDT") {
        dispatch(
          sendUSDT({
            data: {
              pin,
              amount: sendAmount,
              recipientAddress,
            },
            navigate,
          })
        );
      }
    }
  };

  return (
    <div className="pin-container">
      <div className="transparent-circle" />
      <div className="pin-content">
        <div className="login-info-container">
          <img
            src={image ? require(`../../assets/avatars/${image}`) : Avatar}
          />
          <div className="login-info-name">{name}</div>
          <div className="login-info-email">{email}</div>
        </div>
        <div className="pin-inputs-container">
          <FourDigits digits={confirmPinDigits} setDigits={setDigits} />
        </div>
        <div className="confirm-pin-note">
          <span> Note:</span> You are about to send {totalAmount} {selectedCoin}{" "}
          to {recipientAddress}
        </div>
        <div className="pin-btn-container">
          <Button text={"Confirm"} onClick={onClick} />
        </div>
      </div>
    </div>
  );
};

export default ConfirmPin;
