import React, { Fragment, useEffect } from "react";
import dollarImg from "../../assets/dollar.png";
import "./index.css";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {
  faBell,
  faChevronLeft,
  faExchange,
  faPaperPlane,
  faQrcode,
} from "@fortawesome/free-solid-svg-icons";
import Select from "react-select";
import CoinItemChart from "../../components/CoinItemChart";
import TransactionItem from "../../components/TransactionItem";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import {
  getETHBalance,
  getETHPrices,
  getEthTransactions,
  setETH,
} from "../../redux/actions/ethAction";
import {
  getBTCBalance,
  getBTCPrices,
  getBtcTransactions,
  setBtc,
} from "../../redux/actions/btcAction";
import {
  getUSDTBalance,
  getUSDTPrices,
  getUsdtTransactions,
  setUSDT,
} from "../../redux/actions/usdtAction";
import { formatNumberWithCommas } from "../../utils/helper";
import Pagination from "../../components/Pagination";
import currencies from "../../constants/currencies";

const CoinWallet = () => {
  const User = useSelector(({ User }) => User);
  const Eth = useSelector(({ Eth }) => Eth);
  const Btc = useSelector(({ Btc }) => Btc);
  const Usdt = useSelector(({ Usdt }) => Usdt);
  const { selectedCoin, currentUser } = User;
  const { currency, currencySymbol } = currentUser;
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const selectOptions = [
    { value: "usdt", label: "🇺🇸 USD/USDT" },
    { value: "ngn", label: "🇳🇬 NGN/USDT" },
  ];
  const chartOptions = {
    scales: {
      x: {
        display: true,
        grid: {
          display: false,
        },
      },
      y: {
        display: false,
      },
    },
    plugins: {
      legend: {
        display: false, // Hide the legend
      },
    },
  };

  useEffect(() => {
    if (selectedCoin === "BTC") {
      dispatch(
        getETHPrices({
          currency,
        })
      );
      dispatch(getBTCBalance());
      dispatch(getBtcTransactions());
    } else if (selectedCoin === "ETH") {
      dispatch(
        getBTCPrices({
          currency,
        })
      );
      dispatch(getETHBalance());
      dispatch(getEthTransactions());
    } else if (selectedCoin === "USDT") {
      dispatch(
        getUSDTPrices({
          currency,
        })
      );
      dispatch(getUSDTBalance());
      dispatch(getUsdtTransactions());
    }
  }, []);

  const coinMappings = {
    BTC: {
      coinName: "Bitcoin",
      circleColor: "yellow-circle",
      baseColor: "yellow",
      borderColor: "rgba(244, 183, 63, 1)",
      backgroundColor: "rgba(244, 183, 63, 0.2)",
      selectedReducer: Btc,
    },
    ETH: {
      coinName: "Ethereum",
      circleColor: "blue-circle",
      baseColor: "blue",
      borderColor: "rgba(0, 107, 250, 1)",
      backgroundColor: "rgba(0, 107, 250, 0.2)",
      selectedReducer: Eth,
    },
    USDT: {
      coinName: "Tether",
      circleColor: "green-circle",
      baseColor: "green",
      borderColor: "rgba(75, 192, 192, 1)",
      backgroundColor: "rgba(75, 192, 192, 0.2)",
      selectedReducer: Usdt,
    },
  };

  const selectedCoinData = coinMappings[selectedCoin];

  const {
    coinName,
    circleColor,
    baseColor,
    borderColor,
    backgroundColor,
    selectedReducer,
  } = selectedCoinData;

  const {
    prices,
    labels,
    priceNow,
    percentageDiff,
    balance,
    currentTransactionPage,
    transactions,
    paginatedTransactions,
    totalTransactionPages,
    transactionsPerPage,
  } = selectedReducer;

  // Check if difference is positive or negative
  const parsedPercentageDiff = parseFloat(percentageDiff);
  const isPositive = !isNaN(parsedPercentageDiff) && parsedPercentageDiff >= 0;
  // Format Balance
  let formattedBalance = parseFloat(balance).toFixed(7);
  if (Number.isInteger(parseFloat(formattedBalance))) {
    formattedBalance = parseFloat(formattedBalance).toString();
  }

  let balanceCurrency = priceNow * balance;
  let formattedBalanceCurrency = parseFloat(balanceCurrency).toFixed(0);
  if (Number.isInteger(parseFloat(formattedBalanceCurrency))) {
    formattedBalanceCurrency = parseFloat(formattedBalanceCurrency).toString();
  }

  const onSendClick = () => {
    navigate("/sendCoin");
  };
  const setTotalPages = (totalpages) => {
    if (selectedCoin === "BTC") {
      dispatch(
        setBtc({
          name: "totalTransactionPages",
          value: totalpages,
        })
      );
    } else if (selectedCoin === "ETH") {
      dispatch(
        setETH({
          name: "totalTransactionPages",
          value: totalpages,
        })
      );
    } else if (selectedCoin === "USDT") {
      dispatch(
        setUSDT({
          name: "totalTransactionPages",
          value: totalpages,
        })
      );
    }
  };
  const setCurrentPage = (currentPage) => {
    if (selectedCoin === "BTC") {
      dispatch(
        setBtc({
          name: "currentTransactionPage",
          value: currentPage,
        })
      );
    } else if (selectedCoin === "ETH") {
      dispatch(
        setETH({
          name: "currentTransactionPage",
          value: currentPage,
        })
      );
    } else if (selectedCoin === "USDT") {
      dispatch(
        setUSDT({
          name: "currentTransactionPage",
          value: currentPage,
        })
      );
    }
  };
  const setpaginatedItems = (paginatedItems) => {
    if (selectedCoin === "BTC") {
      dispatch(
        setBtc({
          name: "paginatedTransactions",
          value: paginatedItems,
        })
      );
    } else if (selectedCoin === "ETH") {
      dispatch(
        setETH({
          name: "paginatedTransactions",
          value: paginatedItems,
        })
      );
    } else if (selectedCoin === "USDT") {
      dispatch(
        setUSDT({
          name: "paginatedTransactions",
          value: paginatedItems,
        })
      );
    }
  };
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div
          className={`wallet-header-container ${baseColor} coin-wallet-header`}
        >
          <div className={`wallet-header-circle ${circleColor}`} />
          <div className="wallet-header-content">
            <div className="coinwallet-header-row">
              <div
                className="wallet-header-icon-container back"
                onClick={() => navigate("/wallets")}
              >
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
              <div className="coinwallet-coin-name">{coinName}</div>
              <div className="wallet-header-icon-container">
                <FontAwesomeIcon icon={faBell} />
              </div>
            </div>
            <div className="coinwallet-balance">
              {formattedBalance} {selectedCoin}
            </div>
            <div className="coinwallet-balance-currency">
              {currencySymbol}
              {formattedBalanceCurrency} {currency}
            </div>
            <div className="coinwallet-header-btn-container">
              <div
                className={`coinwallet-header-btn ${baseColor}`}
                onClick={onSendClick}
              >
                <div className="coinwallet-header-btn-icon-container">
                  <FontAwesomeIcon icon={faPaperPlane} />
                </div>
                <div className="coinwallet-header-btn-text">Send</div>
              </div>
              <div
                className={`coinwallet-header-btn ${baseColor}`}
                onClick={() => navigate("/recieve")}
              >
                <div className="coinwallet-header-btn-icon-container">
                  <FontAwesomeIcon icon={faQrcode} />
                </div>
                <div className="coinwallet-header-btn-text">Recieve</div>
              </div>
              <div className={`coinwallet-header-btn ${baseColor}`}>
                <div className="coinwallet-header-btn-icon-container">
                  <FontAwesomeIcon icon={faExchange} />
                </div>
                <div className="coinwallet-header-btn-text">Swap</div>
              </div>
            </div>
          </div>
        </div>
        <div className="coinwallet-body-container">
          <div className="coinwallet-coinprice-row">
            <div className="coinwallet-coinprice-dropdown">
              <Select options={currencies} />
            </div>
            <div className="coinwallet-coinprice-value">
              {currencySymbol}
              {formatNumberWithCommas(priceNow)}
            </div>
          </div>
          <div
            className={`coinwallet-coinprice-variation ${
              isPositive && "isPositive"
            }`}
          >
            {percentageDiff}
          </div>
          <div className="coinwallet-chart-container">
            <CoinItemChart
              options={chartOptions}
              backgroundColor={backgroundColor}
              borderColor={borderColor}
              prices={prices}
              labels={labels}
            />
          </div>
          <div className="coinwallet-transactions-container">
            <div className="coinwallet-transactions-title">Transactions</div>
            <div className="coinwallet-transactions-list">
              {paginatedTransactions.map((transaction, index) => (
                <TransactionItem
                  transaction={transaction}
                  priceNow={priceNow}
                  key={index}
                />
              ))}
            </div>
            <div className={`coinwallet-pagination-container ${baseColor}`}>
              <Pagination
                currentPage={currentTransactionPage}
                setCurrentPage={setCurrentPage}
                items={transactions}
                totalPages={totalTransactionPages}
                setTotalPages={setTotalPages}
                itemsPerPage={transactionsPerPage}
                setpaginatedItems={setpaginatedItems}
              />
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default CoinWallet;
