import React, { Fragment } from "react";
import "../Languages/index.css";
import Select from "react-select";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBank, faChevronLeft } from "@fortawesome/free-solid-svg-icons";
import currencies from "../../constants/currencies";
import Button from "../../shared/Button";
import withTabBar from "../../HOC/TabBar";
import "./index.css";
import Input from "../../shared/Input";
import BitcoinImg from "../../assets/bitcoin.png";
import USDTImg from "../../assets/usdt.png";
import { useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import BankItem from "../../components/BankItem";
import { getCoinPrice, setUser } from "../../redux/actions/userAction";
import { setBtc } from "../../redux/actions/btcAction";
import { setETH } from "../../redux/actions/ethAction";
import { setUSDT } from "../../redux/actions/usdtAction";

const Exchange = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const customStyles = {
    control: (provided) => ({
      ...provided,
      height: "70px",
      width: "120px",
    }),
    menuPortal: (base) => ({ ...base, zIndex: 9999 }),
  };
  const currencies = [
    { value: "USD", label: "🇺🇸 USD", symbol: "$" },
    { value: "NGN", label: "🇳🇬 NGN", symbol: "₦" },
  ];
  const coins = [
    { value: "BTC", label: "BTC", image: BitcoinImg },
    { value: "USDT", label: "USDT", image: USDTImg },
  ];
  const User = useSelector(({ User }) => User);
  const {
    selectedBank,
    isExchangeBuy,
    currencyExchangeSelect,
    currencyExchange,
    coinExchangeSelect,
    coinExchange,
    coinExchangePrice,
    settings,
  } = User;
  const { BTC_ADDRESS, USDT_ADDRESS } = settings;
  const onAccordianClick = () => {
    dispatch(
      setUser({
        name: "isExchangeBuy",
        value: !isExchangeBuy,
      })
    );
  };
  const onCurrencyChange = (selectedCurrency) => {
    dispatch(
      setUser({
        name: "currencyExchangeSelect",
        value: selectedCurrency,
      })
    );
    const currency = selectedCurrency.value;
    const coin = coinExchangeSelect.value;
    getCoinCurrentPrice({ coin, currency });
  };
  const onCoinChange = (selectedCoin) => {
    dispatch(
      setUser({
        name: "coinExchangeSelect",
        value: selectedCoin,
      })
    );
    const currency = currencyExchangeSelect.value;
    const coin = selectedCoin.value;
    getCoinCurrentPrice({ coin, currency });
  };

  const onInpuCoinChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    dispatch(
      setUser({
        name,
        value,
      })
    );
    let currencyValue = parseFloat(coinExchangePrice * value).toFixed(2);
    if (Number.isInteger(parseFloat(currencyValue))) {
      currencyValue = parseFloat(currencyValue).toString();
    }
    dispatch(
      setUser({
        name: "currencyExchange",
        value: currencyValue,
      })
    );
  };

  const onInpuCurrencyChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    dispatch(
      setUser({
        name,
        value,
      })
    );
    let coinValue = parseFloat(value / coinExchangePrice).toFixed(7);
    if (Number.isInteger(parseFloat(coinValue))) {
      coinValue = parseFloat(coinValue).toString();
    }
    dispatch(
      setUser({
        name: "coinExchange",
        value: coinValue,
      })
    );
  };

  const getCoinCurrentPrice = ({ coin, currency }) => {
    dispatch(getCoinPrice({ data: { coin, currency } }));
  };

  const onExchangeClick = () => {
    if (isExchangeBuy) {
      navigate("/infoPrompt");
    } else {
      const selectedCoin = coinExchangeSelect.value;
      dispatch(
        setUser({
          name: "selectedCoin",
          value: selectedCoin,
        })
      );
      dispatch(
        setUser({
          name: "isSellTransaction",
          value: true,
        })
      );
      if (selectedCoin === "BTC") {
        dispatch(
          setBtc({
            name: "sendAmount",
            value: coinExchange,
          })
        );
        dispatch(
          setBtc({
            name: "recipientAddress",
            value: BTC_ADDRESS,
          })
        );
      } else if (selectedCoin === "USDT") {
        dispatch(
          setUSDT({
            name: "sendAmount",
            value: coinExchange,
          })
        );
        dispatch(
          setUSDT({
            name: "recipientAddress",
            value: USDT_ADDRESS,
          })
        );
      }
      navigate("/reviewTransactions");
    }
  };

  return (
    <Fragment>
      <div className="coinwallet-container">
        <div className="wallet-header-container light-blue exchange-wallet-header">
          <div className="wallet-header-circle lightblue-circle" />
          <div className="wallet-header-content">
            <div className="preference-header-container">
              <div className="preference-icon-container">
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
              <div className="preference-header-text">Exchange</div>
            </div>
          </div>
        </div>
        <div className="exchange-body-container">
          <div className="exchange-accordian-container">
            <div className="exchange-accordian-content">
              <div
                className={`exchange-accordian-item ${
                  isExchangeBuy && "selected"
                }`}
                onClick={onAccordianClick}
              >
                Buy
              </div>
              <div
                className={`exchange-accordian-item ${
                  !isExchangeBuy && "selected"
                }`}
                onClick={onAccordianClick}
              >
                Sell
              </div>
            </div>
          </div>
          <div
            className={`exchange-inputs-container ${
              !isExchangeBuy && "reverse"
            } `}
          >
            <div className="exchange-input-item">
              <div className="exchange-input-select">
                <Select
                  options={currencies}
                  menuPortalTarget={document.body}
                  styles={customStyles}
                  onChange={onCurrencyChange}
                  value={currencyExchangeSelect}
                />
              </div>
              <div className="exchange-input">
                <Input
                  dir={"rtl"}
                  placeholder={"0.0"}
                  className={"exchange-textbox"}
                  name={"currencyExchange"}
                  onChange={onInpuCurrencyChange}
                  value={currencyExchange}
                />
              </div>
            </div>
            <div className="exchange-input-item">
              <div className="exchange-input-select">
                <Select
                  options={coins}
                  menuPortalTarget={document.body}
                  styles={customStyles}
                  formatOptionLabel={(coin) => (
                    <div className="exchange-coin-option">
                      <img
                        src={coin.image}
                        alt="exchange-coin-image"
                        className="exchange-coin-image"
                      />
                      <span>{coin.label}</span>
                    </div>
                  )}
                  onChange={onCoinChange}
                  value={coinExchangeSelect}
                />
              </div>
              <div className="exchange-input">
                <Input
                  dir={"rtl"}
                  placeholder={"0.0"}
                  className={"exchange-textbox"}
                  name={"coinExchange"}
                  onChange={onInpuCoinChange}
                  value={coinExchange}
                />
              </div>
            </div>
          </div>
          <div className="exchange-actions-container">
            {selectedBank ? (
              <div onClick={() => navigate("/banks")}>
                <BankItem bank={selectedBank} isSelected={true} />
              </div>
            ) : (
              <div
                className="exchange-addbank-container"
                onClick={() => navigate("/banks")}
              >
                <FontAwesomeIcon icon={faBank} /> <span> Choose Bank</span>
              </div>
            )}
            <div className="exchange-btn-container">
              <Button
                text={`${isExchangeBuy ? "Buy" : "Sell"} Now`}
                onClick={onExchangeClick}
              />
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default withTabBar(Exchange);
