import React, { Fragment, useEffect } from "react";
import "../../pages/Languages/index.css";
import "./index.css";
import Select from "react-select";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronLeft } from "@fortawesome/free-solid-svg-icons";
import languages from "../../constants/languages";
import Button from "../../shared/Button";
import withTabBar from "../../HOC/TabBar";
import Input from "../../shared/Input";
import { useDispatch, useSelector } from "react-redux";
import {
  addUserBank,
  getAllBanks,
  setAddBank,
} from "../../redux/actions/userAction";
import { useNavigate } from "react-router-dom";

const AddBank = () => {
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const User = useSelector(({ User }) => User);
  const { addBank } = User;
  const { accountHolder, bankName, accountNumber } = addBank;

  const onChange = (e) => {
    const name = e.target.name;
    const value = e.target.value;
    dispatch(
      setAddBank({
        name,
        value,
      })
    );
  };
  const onClick = () => {
    dispatch(
      addUserBank({
        data: { accountHolder, bankName, accountNumber },
        navigate,
      })
    );
  };
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div className="wallet-header-container light-blue coin-wallet-header">
          <div className="wallet-header-circle lightblue-circle" />
          <div className="wallet-header-content">
            <div className="preference-header-container">
              <div className="preference-icon-container">
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
              <div className="preference-header-text">Add Bank</div>
            </div>
          </div>
        </div>
        <div className="coinwallet-body-container">
          <div className="preference-content-container">
            <div className="auth-form-item">
              <div className="auth-label">Account Holder Name:</div>
              <Input
                placeholder={"Enter Account Holder Name"}
                type={"text"}
                onChange={onChange}
                name={"accountHolder"}
                value={accountHolder}
              />
            </div>
            <div className="auth-form-item">
              <div className="auth-label">Bank Name:</div>
              <Input
                placeholder={"Enter Bank Name"}
                type={"text"}
                onChange={onChange}
                name={"bankName"}
                value={bankName}
              />
            </div>
            <div className="auth-form-item">
              <div className="auth-label">Account Number:</div>
              <Input
                placeholder={"Enter Account Number"}
                type={"text"}
                onChange={onChange}
                name={"accountNumber"}
                value={accountNumber}
              />
            </div>
            <div className="addBank-btn-container">
              <div className="addBank-btn-item">
                <Button text={"Add Bank"} onClick={onClick} />
              </div>
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default withTabBar(AddBank);
