import React, { Fragment } from "react";
import AuthImg from "../../assets/emailVerification.png";
import Input from "../../shared/Input";
import "./index.css";
import Button from "../../shared/Button";
import { faEnvelope, faLock } from "@fortawesome/free-solid-svg-icons";
import FourDigits from "../../shared/FourDigits";
import { useDispatch, useSelector } from "react-redux";
import { checkOTP, setUser } from "../../redux/actions/userAction";
import { useNavigate } from "react-router-dom";

const EmailVerification = () => {
  const User = useSelector(({ User }) => User);
  const { emailVerificationDigits, forgotPasswordEmail } = User;
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const setDigits = (digits) => {
    dispatch(
      setUser({
        name: "emailVerificationDigits",
        value: digits,
      })
    );
  };
  const onClick = () => {
    const otp = parseInt(emailVerificationDigits.join(""), 10);
    dispatch(
      checkOTP({
        data: {
          email: forgotPasswordEmail,
          otp,
        },
        navigate,
      })
    );
  };
  return (
    <Fragment>
      <div className="auth-image-container forgotpass">
        <img src={AuthImg} className="email-image" />
      </div>
      <div className="email-content-container">
        <div className="email-text-container">
          <div className="email-title">Email Password</div>
          <div className="email-desc">
            Please enter the 4 digit code sent to your email address.
          </div>
        </div>
        <div className="auth-form-item">
          <div className="digit-input-container">
            <FourDigits
              digits={emailVerificationDigits}
              setDigits={setDigits}
            />
          </div>
        </div>
        <div className="resend-text">
          If you didn't receive code!{" "}
          <span className="resend-link">Resend</span>{" "}
        </div>
        <div className="auth-form-item">
          <Button text={"Verify and Proceed"} onClick={onClick} />
        </div>
        <div className="auth-alternate-link"></div>
      </div>
    </Fragment>
  );
};

export default EmailVerification;
