import React, { Fragment } from "react";
import "../Languages/index.css";
import Select from "react-select";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faChevronLeft, faCopy } from "@fortawesome/free-solid-svg-icons";
import currencies from "../../constants/currencies";
import Button from "../../shared/Button";
import withTabBar from "../../HOC/TabBar";
import "./index.css";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { buyCrypto } from "../../redux/actions/userAction";
import { useNavigate } from "react-router-dom";

const InfoPrompt = () => {
  const User = useSelector(({ User }) => User);
  const dispatch = useDispatch();
  const navigate = useNavigate();
  const {
    currencyExchange,
    currencyExchangeSelect,
    selectedBank,
    coinExchangeSelect,
  } = User;
  const { symbol, value } = currencyExchangeSelect;
  const currency = value;
  const { accountHolder, bankName, accountNumber } = selectedBank;
  const onTransfer = () => {
    const amount = parseFloat(currencyExchange);
    const coin = coinExchangeSelect.value;
    console.log({
      accountHolder,
      bankName,
      accountNumber,
      symbol,
      currency,
      amount,
      coin,
    });
    dispatch(
      buyCrypto({
        data: {
          accountHolder,
          bankName,
          accountNumber,
          currency,
          amount: currencyExchange,
          coin,
        },
        navigate,
      })
    );
  };
  const CopyToClipboard = (text) => {
    navigator.clipboard.writeText(text);
    toast.success("Address was copied to clipboard", {
      position: "top-right",
      autoClose: 2000,
      hideProgressBar: false,
      closeOnClick: true,
      pauseOnHover: true,
      progress: undefined,
      theme: "light",
    });
  };
  return (
    <Fragment>
      <div className="coinwallet-container">
        <div className="wallet-header-container light-blue coin-wallet-header">
          <div className="wallet-header-circle lightblue-circle" />
          <div className="wallet-header-content">
            <div className="preference-header-container">
              <div className="preference-icon-container">
                <FontAwesomeIcon icon={faChevronLeft} />
              </div>
              <div className="preference-header-text">Buy Crypto</div>
            </div>
          </div>
        </div>
        <div className="coinwallet-body-container">
          <div className="InfoPrompt-header">Pay the Seller</div>
          <div className="infoPrompt-amount">
            {symbol}
            {currencyExchange}
            <div
              className="infoPrompt-icon-container"
              onClick={() => CopyToClipboard(currencyExchange)}
            >
              <FontAwesomeIcon icon={faCopy} />
            </div>
          </div>
          <div className="infoPrompt-desc">
            Leave the Coinaik Application,open your selected banking or payment
            platform that matches your verified name on Coinatik, and transfer
            the funds to the sellers account below
          </div>
          <div className="infoPrompt-card">
            <div className="infoPrompt-text-header">Bank Transfer</div>
            <div className="infoPrompt-item">
              <div className="infoPrompt-name">Account Holder Name</div>
              <div className="infoPrompt-value">
                Abdullah Abid
                <div className="infoPrompt-icon-container">
                  <FontAwesomeIcon icon={faCopy} />
                </div>
              </div>
            </div>
            <div className="infoPrompt-item">
              <div className="infoPrompt-name">Account Number</div>
              <div className="infoPrompt-value">
                3740587241313
                <div className="infoPrompt-icon-container">
                  <FontAwesomeIcon icon={faCopy} />
                </div>
              </div>
            </div>
            <div className="infoPrompt-item">
              <div className="infoPrompt-name">Bank Name</div>
              <div className="infoPrompt-value">
                Faysal Bank
                <div className="infoPrompt-icon-container">
                  <FontAwesomeIcon icon={faCopy} />
                </div>
              </div>
            </div>
          </div>
          <div className="infoPrompt-btn-row">
            <div className="infoPrompt-btn-item">
              <Button text={"Transfered,Notify Seller"} onClick={onTransfer} />
            </div>
            <div className="infoPrompt-btn-item">
              <Button text={"Cancel"} className={"red"} />
            </div>
          </div>
        </div>
      </div>
    </Fragment>
  );
};

export default InfoPrompt;
